.prices {
    padding-top: 18px;
    padding-bottom: 60px;

    @include md-block() {
        padding-top: 122px;
        padding-bottom: 90px; }

    &__container {
        max-width: 900px;
        margin-left: auto;
        margin-right: auto;
        padding-left: 18px;
        padding-right: 18px; }

    &__title {
        font-size: 20px;
        line-height: 30px;
        font-family: $bold;
        margin-top: 0;
        margin-bottom: 0;
        margin-bottom: 14px;
        color: #ffffff;
        text-transform: uppercase;


        @include md-block() {
            margin-bottom: 42px;
            font-size: 27px;
            line-height: 40px; }

        span {
            background-color: $purple-text;
            line-height: 32px;

            @include md-block() {
                line-height: 40px;
                padding-left: 5px;
                padding-right: 5px; } } }

    &__table-container {

        @include md-block() {
            background-image: repeating-linear-gradient(-48deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(42deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(132deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(222deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px);
            background-size: 1px 100%, 100% 1px, 1px 100% , 100% 1px;
            background-position: 0 0, 0 0, 100% 0, 0 100%;
            background-repeat: no-repeat; } }

    &__table-title {
        display: none;

        @include md-block() {
            display: flex;
            justify-content: space-between;
            font-size: 20px;
            line-height: 30px;
            font-family: $bold;
            text-transform: uppercase;
            color: #242424;
            margin-bottom: 8px;
            margin-top: 10px; } }

    &__table {
        padding-left: 18px;
        padding-right: 34px;
        padding-top: 8px;
        padding-bottom: 22px;
        background-image: repeating-linear-gradient(-48deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(42deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(132deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(222deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px);
        background-size: 1px 100%, 100% 1px, 1px 100% , 100% 1px;
        background-position: 0 0, 0 0, 100% 0, 0 100%;
        background-repeat: no-repeat;

        @include md-block() {
            background-image: none;
            padding-left: 26px;
            padding-right: 26px; }

        + .prices__table {
            margin-top: 20px; } }

    &__table-total {
        font-size: 26px;
        line-height: 38px;
        color: $black-bg;
        font-family: $bold;
        text-align: right;
        margin-top: 38px;
        display: flex;
        justify-content: flex-end;

        @include md-block() {
            margin-right: 36px;
            margin-top: 16px;
            padding-bottom: 32px;
            margin-top: 0; } }

    &__table-total-value {

        position: relative;
        text-transform: uppercase;


        &::before {
            position: absolute;
            content: '';
            width: calc(100% + 64px);
            height: calc(100% + 30px);
            left: -45px;
            top: -14px;
            background: url('../img/decor/price.svg') no-repeat center;
            background-size: 100% 100%;
            pointer-events: none; } } }




