.accordeon {
    margin: 0;
    padding: 0;
    list-style: none;

    @include md-block() {
        padding-top: 12px;
        padding-bottom: 12px;
        padding-left: 40px;
        padding-right: 58px;
        border-width: 15px;
        background-image: repeating-linear-gradient(-48deg, $green-text, $green-text 15px, transparent 15px, transparent 30px, $green-text 30px), repeating-linear-gradient(42deg, $green-text, $green-text 15px, transparent 15px, transparent 30px, $green-text 30px), repeating-linear-gradient(132deg, $green-text, $green-text 15px, transparent 15px, transparent 30px, $green-text 30px), repeating-linear-gradient(222deg, $green-text, $green-text 15px, transparent 15px, transparent 30px, $green-text 30px);
        background-size: 1px 100%, 100% 1px, 1px 100% , 100% 1px;
        background-position: 0 0, 0 0, 100% 0, 0 100%;
        background-repeat: no-repeat; }


    &__item {
        border-top: 1px solid $green-text;

        &:last-child {
            border-bottom: 1px solid $green-text;

            @include md-block() {
                border-bottom: none; } }

        &:first-child {
            @include md-block() {
                border-top: none; } } }



    &__header {
        display: flex;
        align-items: center;
        background-color: transparent;
        padding-left: 0;
        padding-right: 0;
        border: none;
        width: 100%;
        padding-right: 12px;
        padding-top: 9px;
        padding-bottom: 9px;
        cursor: pointer;

        &:hover {
            background-color: $green-text;

            .accordeon__header-text {
                color: #000000; }

            .accordeon__header-number {
                color: #000000; }

            .accordeon__header-icon svg {
                fill: $black-text; } }


        @include md-block() {
            padding-top: 16px;
            padding-bottom: 16px; }

        &.active + .accordeon__item-content {
 } }            // display: block

    &__header-container {
        display: flex;
        align-items: baseline;
        padding-right: 20px; }


    &__header-text {
        font-size: 13px;
        line-height: 20px;
        font-family: $semibold;
        color: $white-text;
        text-transform: uppercase;
        text-align: left;

        @include md-block() {
            font-size: 22px;
            line-height: 32px; } }

    &__header-number {
        font-size: 13px;
        line-height: 20px;
        font-family: $semibold;
        color: $green-text;
        padding-right: 12px;
        align-self: flex-start;

        @include md-block() {
            font-size: 22px;
            line-height: 32px;
            padding-right: 60px;
            margin-top: 0; } }


    &__header-icon {
        width: 24px;
        height: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: auto;
        align-self: center;
        background-color: transparent;

        svg {
            width: 24px;
            height: 24px;
            fill: $green-text; }

        @include md-block() {
            width: 32px;
            height: 32px; } }


    &__item-content {
        display: none;
        margin-left: auto;
        margin-right: auto;
        max-width: 922px;
        overflow: hidden;
        box-sizing: border-box; }



    &__item-content-inner {
        padding-top: 20px;
        padding-bottom: 50px;

        @include md-block() {
            width: 100%;
            display: flex;
            column-gap: 50px; } }




    &__item-info {
        font-size: 16px;
        line-height: 24px;

        @include md-block() {
            max-width: 515px;
            width: 100%;
            font-size: 20px;
            line-height: 30px; } }

    &__item-info-price {
        margin-top: 24px;

        b {
            font-family: $bold;
            color: $green-text; } }

    &__item-info-value {
        margin-left: 8px; }

    &__item-video {
        margin-top: 32px;

        @include md-block() {
            margin-top: 0;
            max-width: 358px;
            width: 100%; } }

    &__item-video-container {
        max-width: 500px; }

    &__item-video-container-inner {
        position: relative;
        padding-top: 55.8%;
        overflow: hidden;

        img {
            position: absolute;
            width: 100%;
            height: 100%;
            content: '';
            left: 0;
            top: 0; } }

    &__item-video-link-container {
        margin-top: 16px;

        @include md-block() {
            display: flex;
            justify-content: flex-end; } }

    &__item-video-link {
        font-size: 20px;
        line-height: 30px;
        border: 1px solid $green-text;
        display: flex;
        align-items: center;
        text-decoration: none;
        color: #ffffff;
        padding-left: 12px;
        padding-right: 16px;
        padding-bottom: 7px;
        padding-top: 2px;

        justify-content: center;

        @include md-block() {
            margin-left: auto; } }


    &__item-video-icon {
        display: flex;
        width: 22px;
        height: 22px;
        justify-content: center;
        align-content: center;
        margin-right: 15px;


        svg {
            width: 22px;
            height: 22px;
            fill: $green-text; } }

    &--light {
        @include md-block() {
            background-image: repeating-linear-gradient(-48deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(42deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(132deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px), repeating-linear-gradient(222deg, $stroke-dark, $stroke-dark 15px, transparent 15px, transparent 30px, $stroke-dark 30px); }


        .accordeon__item {
            color: $black-text;
            border-color: $black-text; }

        .accordeon__item-info-price b {
            color: $purple-text; }

        .accordeon__item-video-link {
            border-color: $purple-text;
            color: $black-text;

            svg {
                fill: $purple-text; } }

        .accordeon__header {

            &:hover {
                background-color: $purple-text;

                .accordeon__header-number {
                    color: $white-text; }

                .accordeon__header-text {
                    color: $white-text; }

                .accordeon__header-icon svg {
                    fill: #ffffff; } } }

        .accordeon__header-number {
            color: $gray-text; }

        .accordeon__header-text {
            color: $black-text; }

        .accordeon__header-icon svg {
            fill: $black-text; } } }


